<template>
  <div style="margin-top: 15%;background-color: #fcb59c">
    <el-container>
      <el-header>注册</el-header>
      <el-container>
        <el-main>
          <div>
            <div class="input">
              用户名：
              <el-input size="mini" v-model="spduser.username" clearable="true" @blur="vility"></el-input>
            </div>
            <div class="input">
              密码：
              <el-input size="mini" v-model="spduser.password" class="password" clearable="true"></el-input>
            </div>
            <div class="input">
              邮箱：
              <el-input size="mini" v-model="spduser.email" class="password" clearable="true"></el-input>
            </div>
            <div class="button">
              <el-button type="success" round>注册</el-button>
              <el-button type="info" round>取消</el-button>
            </div>
          </div>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
  export default {
    name: "BlogRegist",
    data() {
      return {
        spduser: {
          username: '',
          password: '',
          email: ''
        }

      }
    },
    methods: {
      vility: function () {
        //后台验证当前用户名是否存在
        this.$axios.get('/regist/' + this.username
        ).then(successResponse => {
          if (successResponse.data.code === 400) {
            this.$alert(successResponse.data.message)
          } else {
          }
        })
      }
    }
  }
</script>

<style>
  .el-input {
    width: 50%;
    margin: 0.8%;
  }

  .el-container {
    width: 60%;
    margin-left: 20%;
  }

  .el-header {
    background-color: #fcd1c7;
    width: 60%;
    margin-left: 20%;
    text-align: center;
    padding-top: 20px;

  }

  .el-main {
    background-color: #fcf2d2;
    width: 100%;
  }

  .password {
    margin-left: 4.7%;
    width: 50%;
    margin-bottom: 5%;
  }

  .button {
    text-align: center;
  }

  .input {
    margin-left: 20%;
  }
</style>



